/**
 * Session ID normalization utilities
 *
 * Provides functions to normalize session IDs to UUID format.
 * Uses UUID v5 (RFC 4122) for deterministic conversion of non-UUID session IDs.
 */

import { validate as isValidUUID, v5 as uuidv5 } from "uuid";
import { ZEST_SESSION_NAMESPACE } from "../config/constants.js";

/**
 * Check if a session ID is already a valid UUID
 */
export function isValidSessionId(sessionId: string): boolean {
  return isValidUUID(sessionId);
}

/**
 * Normalize a session ID to UUID format
 *
 * If the session ID is already a valid UUID, it is returned as-is.
 * Otherwise, a deterministic UUID v5 is generated from the session ID.
 *
 * @param sessionId - The session ID to normalize
 * @returns A valid UUID string
 */
export function normalizeSessionId(sessionId: string): string {
  // If already a valid UUID, return as-is
  if (isValidUUID(sessionId)) {
    return sessionId;
  }

  // Generate deterministic UUID v5 from the session ID
  return uuidv5(sessionId, ZEST_SESSION_NAMESPACE);
}
